.card-succ {
    background: #bbf7db;
    border-top: 5px solid #049669;
    border-radius: 10px;
}

.card-warn {
    background: #fed3d3;
    border-top: 5px solid #db2625;
    border-radius: 10px;
}

.icono {
    font-size: 45px;
    color: white;
    background: #db2625;
    border-radius: 50px;
    padding: 10px;
    justify-content: center;
    align-items: center;
}

